@keyframes scroll-down-arrow {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(15px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes header-animation {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}
